<template>
  <div id="app">
    <Header class="header-custom" :class="{'header-style-scroll': isScroll}"></Header>
    <!-- <div class="carousel-wrapper">
      <Carousel class="carousel-custom"></Carousel>
    </div> -->
    <router-view :class="{'page-style-scroll': isScroll}"></router-view>
    <div class="divider-line area-wrapper"></div>
    <Footer></Footer>
  </div>
</template>

<script>
import Header from "./components/header";
import Carousel from "./components/Carousel";
import Footer from "./components/Footer";
export default {
  name: "app",
  components: {
    Header,
    Carousel,
    Footer
  },
  data() {
    return {
      isScroll: false
    };
  },
  created() {
    console.log(this.$router);
  },
  mounted() {
    this.listenScroll();
  },
  watch: {
    $route() {
      let app = document.querySelector("#app");
      app.scrollTop = 0;
    }
  },
  methods: {
    linkto(path) {
      let host = window.location;
      this.$router.push(path);
    },
    listenScroll() {
      let app = document.querySelector("#app");
      let header = document.querySelector(".header-custom");

      app.addEventListener("scroll", e => {
        let scrollTop = app.scrollTop;
        if (scrollTop >= 76) {
          this.isScroll = true;
        } else {
          this.isScroll = false;
        }
      }); 
    }
  }
};
</script>

<style lang="scss" scoped>
#app {
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  .header-style-scroll {
    position: fixed;
    width: 100%;
    left: 0;
    top: 0;
    z-index: 10;
    background: #fff;
    padding-top: 0;
    box-shadow: 0 2px 4px 0 rgba(133, 138, 180, 0.14);
    transition: all 0.6s;
  }
  .page-style-scroll {
    padding-top: 80px;
    transition: all 1s;
  }
}
</style>
