<template>
  <div class="hl-carousel">
    <div class="hl-carousel-list">
      <div v-for="(item, i) in carousellist" :key="i" class="carousel-item" :class="{active: activeIndex === i}">
        <div class="carousel-item-content">
          <div class="desc">
            <h2>{{item.title}}</h2>
            <div class="desc-text">
              <p>{{item.text1}}</p>
              <p v-if="item.text2">{{item.text2}}</p>
            </div>
            <div class="btn">{{item.btnText}}</div>
          </div>
          <div class="img-banner"></div>
        </div>
      </div>
    </div>
    <ul class="slick-dots">
      <li 
        v-for="(item, i) in carousellist"
        :key="i"
        class="dot"
        :class="{active: activeIndex === i}"
        @click="handleDotClick(i)"
      >
        <span></span>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  data() {
    return {
      timer: null,
      activeIndex: 0,
      carousellist: [
        {
          title: "关于恒链",
          text1: "一站式可视化物流SAAS服务提供商",
          text1: "RPA机器人流程自动化   复杂的操作简单化",
          btnText: "免费试用",
          btnLink: ""
        },
        {
          title: "在线订舱",
          text1: "创造性的将互联网与传统的海运相结合",
          text1: "RPA全流程自动化   节省人力成本70%  提升工作效率",
          btnText: "查看详情",
          btnLink: ""
        },
        {
          title: "报价查询",
          text1: "智能实时查询最新运价   为您提供精确的船期报价",
          btnText: "查看详情",
          btnLink: ""
        }
      ]
    };
  },
  mounted() {
    this.AutoBroadCast();
  },
  methods: {
    AutoBroadCast() {
      this.timer && clearInterval(this.timer);
      this.timer = setInterval(() => {
        this.setNextActiveIndex();
      }, 8000);
    },
    handleDotClick(i) {
      this.activeIndex = i;
      this.AutoBroadCast();
    },
    setNextActiveIndex() {
      let index = this.activeIndex;
      if (index >= this.carousellist.length - 1) {
        this.activeIndex = 0;
      } else {
        ++this.activeIndex;
      }
    }
  }
};
</script>

<style lang="scss" scoped>
@import "../style/mixin.scss";
@keyframes progress {
  0% {
    width: 5%;
  }
  100% {
    width: 100%;
  }
}
.hl-carousel {
  position: relative;
  .hl-carousel-list {
    position: relative;
    height: 432px;
    .carousel-item {
      position: absolute;
      top: 0;
      left: 0;
      height: 100%;
      width: 100%;
      z-index: -1;
      background: #fff;
      &.active {
        z-index: 0;
      }

      &:nth-child(1) {
        .img-banner {
          width: 300px;
          height: 254px;
          background: url("../../public/static/img/banner_01.png") no-repeat;
          background-size: 100% 100%;
        }
      }
      &:nth-child(2) {
        .img-banner {
          width: 300px;
          height: 291px;
          background: url("../../public/static/img/banner_02.png") no-repeat;
          background-size: 100% 100%;
        }
      }
      &:nth-child(3) {
        .img-banner {
          width: 300px;
          height: 291px;
          background: url("../../public/static/img/banner_03.png") no-repeat;
          background-size: 100% 100%;
        }
      }
    }
    .carousel-item-content {
      display: flex;
      height: 100%;
      justify-content: space-between;
      align-items: center;
      .desc {
        align-self: center;
        h2 {
          font-size: 36px;
          color: #ef7609;
          margin-bottom: 30px;
        }
        .desc-text {
          font-size: 18px;
          margin-bottom: 30px;
          line-height: 24px;
          color: #5d6494;
        }

        .btn {
          width: 100px;
          height: 36px;
          cursor: pointer;
          transition: all 0.2s;
          background: $btnBgColor;
          box-shadow: 0 3px 4px 0 #c85b0a;
          border-radius: 4px;
          font-size: 13px;
          letter-spacing: 1px;
          text-align: center;
          font-weight: 700;
          line-height: 36px;
          user-select: none;
          color: #fff;
        }
      }
      .img {
        height: 300px;
        width: 260px;
        background: #ebb624;
      }
    }
  }
  .slick-dots {
    position: absolute;
    left: 50%;
    bottom: 40px;
    transform: translateX(-50%);
    z-index: 1;
    display: flex;
    .dot {
      width: 40px;
      height: 16px;
      margin-right: 10px;
      cursor: pointer;
      span {
        position: relative;
        display: inline-block;
        vertical-align: middle;
        width: 100%;
        height: 3px;
        background: #f3dfa7;
        cursor: pointer;
        &::before {
          content: "";
          position: absolute;
          left: 0;
          top: 0;
          display: inline-block;
          height: 100%;
          width: 0;
          background: #ebb624;
        }
      }

      &.active {
        span {
          &::before {
            animation: progress 8s linear;
          }
        }
      }
    }
  }
}
</style>