import Vue from 'vue'
import VueRouter from 'vue-router'
import routes from './routes'

Vue.use(VueRouter)

let base
if (process.env.NODE_ENV === 'development') {
 base = '/'
} else {
  base = '/prerender'
}

const router = new VueRouter({
  base: base,
  mode: 'history',
  routes
})

export default router

